.wrapper {
  position: fixed;
  left: 0;
  bottom: 0;
  // background: linear-gradient(70deg, blue, pink);
  display: flex;
  height: 45px;
  width: 100%;
  // background: linear-gradient(3deg, #e6d16c, #16014c);

