@import url("https://fonts.googleapis.com/css2?family=Cardo:ital,wght@0,400;0,700;1,400&family=Montserrat:ital,wght@1,700&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");

.wrapper {
  display: flex;
  height: 45px;
  width: 100%;
  // background: linear-gradient(3deg, #e6d16c, #16014c);
  // margin-bottom: 30px;
  justify-content: space-around;
  background: -webkit-linear-gradient(90deg, #1a3f69, #5076a1, #1a3f69);
  background: linear-gradient(90deg, #1a3f69, #5076a1, #1a3f69);
}
.sign {
  text-align: center;
  padding-top: 10px;
}
.button_style {
  // background-color: rgba(246, 246, 246, 1);
  // border-width: 0;
  padding-right: 25px;
  // background: transparent;
  background-color: steelblue;
  padding-right: 10px;
  margin-right: 15px;
}
.button_style:hover {
  background: white;
  transition: background-color 0.5s ease-out 0s;
}
.link {
  font-family: Montserrat;
  font-size: 15px;
  color: rgba(0, 0, 0, 1);
  text-decoration: none;
}
.shopping_cart {
  height: 27px;
  width: 27px;
}
.pokeball {
  height: 27px;
  width: 27px;
  margin-left: 15px;
}

.text {
  padding-top: 15px;
  color: white;
  font-family: Montserrat;
  font-size: 15px;
  padding-left: 8px;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px yellow;
}
.text:hover {
  color: red;
}
.first_column {
  display: flex;
}
.second_column {
  display: flex;
  align-items: center;
}
