.personalInfo {
  box-shadow: inset 2px 2px 5px rgb(154 147 140 / 50%),
    1px 1px 5px rgb(255 255 255);
  margin-top: 50px;
  width: 50%;
}
.title {
  color: darkblue;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  padding-bottom: 30px;
  margin-top: 83px;
}
.infoContainer {
  display: flex;
  padding-bottom: 33px;
  padding-left: 30px;
  justify-content: space-evenly;
}

.info {
  box-shadow: inset 2px 2px 5px rgb(154 147 140 / 50%),
    1px 1px 5px rgb(255 255 255);
  padding: 20px 20px 20px 20px;
}
.field {
  margin-top: 15px;
  font-size: 18px;
}
.wrapper {
  display: flex;
  justify-content: space-around;
}
.img {
  height: 450px;
  padding-top: 34px;
}
