.wrapper {
  margin-bottom: 10px;
  color: #1b1b4d;
}

.cartWrapper {
  display: flex;
  gap: 30px;
  justify-content: space-between;

  .cart {
    width: 55%;

    .title {
      color: darkblue;
      padding: 20px;
      margin-bottom: 20px;
      font-size: 30px;
      font-weight: 700;
      text-align: center;
    }
    .titleCart {
      justify-content: right;
      font-size: 30px;
      margin-right: 5px;
      margin-left: 5px;
      margin-bottom: 10px;
      display: flex;
      color: #1a3f69;
    }

    .cardWrapper {
      padding: 20px;
      margin-bottom: 20px;
      margin-left: 5px;
      display: flex;
      justify-content: space-between;
      border: 25px solid steelblue;
      box-shadow: 10px 5px 5px #1a3f69;

      .card {
        display: flex;
        gap: 40px;

        .info {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          font-weight: 600;

          .name {
            font-size: 20px;
          }
        }
      }

      .buttonsWrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-between;
        gap: 50px;
      }
    }
  }

  .orderWripper {
    margin: 50px;
    height: 100px;
    width: 200px;
    display: inline-block;
    box-shadow: inset 2px 2px 5px rgba(154, 147, 140, 0.5),
      1px 1px 5px rgba(255, 255, 255, 1);

    .orderPrice {
      margin-bottom: 25px;
      font-size: 25px;
      font-weight: 600;
      text-align: center;
    }

    .subOrder {
      margin-right: 5px;
      margin-left: 5px;
      margin-bottom: 10px;
      display: flex;
      justify-content: space-between;
      border-bottom: 1.3px solid darkblue;
    }

    .total {
      margin-right: 5px;
      margin-left: 5px;
      margin-bottom: 40px;
      display: flex;
      justify-content: space-between;
      font-weight: 700;
    }

    .checkout {
      width: 100%;
      height: 45px;
      color: #fff;
      background-color: steelblue;
      border: 1px solid black;
      font-weight: 500;
      font-size: 18px;
      cursor: pointer;

      &:hover {
        color: darkblue;
        background: white;
        transition: background-color 0.5s ease-out 0s;
        border: 2px solid steelblue;
      }
    }
  }
}
.img {
  height: 400px;
}
.main_title {
  text-align: right;
}
