.button {
  background-color: aqua;
  margin-top: 10px;
  cursor: pointer;
}
.button:hover {
  background: white;
  transition: background-color 0.5s ease-out 0s;
}
.button:active {
  background: coral;
  transition: background-color 0.5s ease-out 0s;
}
.div {
  justify-content: center;
}
