.home_wrapper {
  text-align: center;
  // display: flex;
  width: 100%;
  height: 524px;
  // background: linear-gradient(90deg, #69b7eb, #b3dbd3, #f4d6db);
  // background-image: url(../../../static/images/main1000_ash-ketchum_3396x1885_6634z.jpg);
