.text_column {
  width: 50%;
  font-family: Montserrat;
  font-size: 35px;
  text-align: center;
}
.all {
  padding-top: 32px;
}
.first_text {
  font-family: Montserrat;
  font-size: 40px;
  color: steelblue;
  text-shadow: 1px 1px 1px #000, -1px 1px 1px #000;
}
.second_text {
  font-family: Montserrat;
  font-size: 40px;
  text-align: center;
  color: steelblue;
  text-shadow: 1px 1px 1px #000, -1px 1px 1px #000;
}
.third_text {
  font-family: Montserrat;
  font-size: 40px;
  color: steelblue;
  text-shadow: 1px 1px 1px #000, -1px 1px 1px #000;
}
.button {
  margin-top: 3px;
  width: 220px;
  height: 30px;
  font-family: Montserrat;
  background-color: steelblue;
  cursor: pointer;
}
.button:hover {
  background: white;
  transition: background-color 0.5s ease-out 0s;
}
