.wrapper {
  display: flex;
  align-items: center;

  .button {
    padding: 8px 15px;
    color: #fff;
    background-color: steelblue;
    cursor: pointer;

    &:hover {
      background: rgb(92, 115, 190);
    }
  }

  .quantity {
    width: 50px;
    padding: 5px;
    text-align: center;
    background-color: #fff;
    border: 1.5px solid #1b1b4d;
  }
}
