.wrapper {
  margin-top: 15px;
  justify-content: center;
  align-items: center;
}

.text {
  display: flex;
  margin-top: 30px;
  border-bottom: 2px solid blueviolet;
}

.text_title {
  margin-left: 5px;
  font-family: Montserrat;
  font-size: 15px;
}
.text_value {
  margin-left: 7px;
  font-family: Montserrat;
  font-size: 16px;
}

.img_column {
  height: 250px;
  margin-left: 55px;
}

.firs_column {
  display: flex;
}

.second_column {
  font-family: Montserrat;
  font-size: 25px;
}
.item {
  margin-left: 25px;
  color: darkblue;
}
.pika_poke {
  width: 148%;
  margin-left: 110px;
}
.pokemon_name {
  font-family: Montserrat;
  color: darkblue;
  text-align: center;
  font-size: 50px;
  margin-top: 18px;
}

//============================================================

.link {
  font-family: Montserrat;
  color: rgba(0, 0, 0, 1);
  text-decoration: none;
}
.button {
  border-radius: 12%;
  margin-left: 58px;
  margin-right: 50px;
  width: 221px;
  height: 37px;
  font-family: Montserrat;
  background-color: steelblue;
  cursor: pointer;
}
.button:hover {
  background: yellow;
  transition: background-color 0.5s ease-out 0s;
}
