.wrapper {
  width: 35%;
  margin: 30px auto;
  text-align: center;
}

.title {
  font-size: 30px;
  color: darkblue;
  line-height: 50px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;

  .inputWrapper {
    display: flex;
    flex-direction: column;
  }

  .button {
    font-family: Montserrat;
    font-size: 18px;
    padding: 10px;
    font-weight: 500;
    font-size: 18px;
    background-color: steelblue;

    &:hover {
      background: rgb(92, 115, 190);
    }
  }
}

.registration {
  padding: 15px;
  color: darkblue;
  font-weight: 500;

  .link {
    color: darkblue;
    margin-left: 10px;
  }
}
